.tarifs {
    width: 100%;
    max-width: 1180px;
    min-width: 292px;
    margin: 10px auto 0 auto;
    padding: 0 0 28px 0;
    border-top: 3px solid white;
    border-bottom: 3px solid white;
}

.tarifs__container__columns {
    display: flex;
    flex-direction: column;
    margin: 0;
    gap: 10px;
    width: calc(15% - 3px);
    text-align: center;
}

.tarifs__value {
    font-size: 18px;
    font-weight: 400;
    color: white;
    margin: 0;
    padding: 0;
}

.tarifs__property {
    font-size: 18px;
    font-weight: bold;
    color: white;
    margin: 0;
    padding: 0;
}