.lofts {
    width: 100%;
    max-width: 880px;
    margin: 10px auto 20px auto;
    padding: 20px 0 30px 0;
    border-top: 3px solid white;
}

.lofts__title {
    font-size: 55px;
    font-weight: 400;
    margin: 0 auto;
    padding: 0 0 15px 0;
    color: white;
    text-align: center;
}

.lofts__container {
    display: flex;
    flex-direction: row;
    gap: 16px;
    margin: 0 auto;
    padding: 0 0 15px;
    justify-content:space-around;
    width: calc(100% - 20px);
}

.lofts__container__element {
    display: flex;
    flex-direction: column;
    gap: 15px;
    text-decoration: none;
}
.lofts__container__title {
    font-size: 25px;
    font-weight: 400;
    margin: 0 auto;
    padding: 0;
    color: white;
    text-align: center;
    font-family: "UnicaOne";
    
}
.lofts__container__image {
    width: 100%;
    max-width: 450px;
    height: 100%;
    max-height: 288px;
    /* animation: fade 5s linear infinite;
    animation-delay: 15s; */
    /* border: 2px solid white; */
    border-radius: 10px;
    box-shadow: 0px 1px 2px 0px rgba(156, 9, 241,0.7),
    1px 2px 4px 0px rgba(156, 9, 241,0.7),
    2px 4px 8px 0px rgba(156, 9, 2415,0.7),
    2px 4px 16px 0px rgba(156, 9, 241,0.7);
}
@keyframes fade {
    0% { opacity: 0.3 }
    25% { opacity: 0.7 }
    50% { opacity: 1 }
    75% { opacity: 0.7 }
    100% { opacity: 0.3 }
}
@media screen and (max-width:700px){
    .lofts__title {
        font-size: 35px;
        padding-bottom: 30px;
    }
    .lofts__container{
        flex-direction: column;
        align-items: center;
        gap: 24px;
    }
}